<template>
    <div>
        <Ueditor 
            :defaultMsg=defaultMsg 
            :config=config
            ref='ue'
        >
        </Ueditor>
    </div>
</template>
<script>
import Ueditor from './editor';
export default {
    data(){
        return{
            defaultMsg:'测试用',
            config:{
                initialFrameWidth:1200,
                initialFrameHeight:400
            }
        }
    },
    components:{
        Ueditor
    },
    mounted(){
    }
}

</script>




